.white {
  color: white;
}

.gray {
  color: gray;
}

.green {
  color: rgba(215, 255, 44, 0.9);
}

@font-face {
  font-family: "hyperspaceregular";
  src: url("../fonts/hyperspace-webfont.woff2") format("woff2"), url("../fonts/hyperspace-webfont.woff") format("woff");
  font-weight: normal, bold;
  font-style: normal;
}
@font-face {
  font-family: "hyperspacebold";
  src: url("../fonts/Hyperspace Bold.otf") format("otf");
  font-weight: bold;
  font-style: normal;
}
body {
  background-color: #1e1e1e;
  overflow: hidden;
}

#game__wrapper {
  background-image: url("../img/space5.jpg");
  background-color: #000000;
  background-size: cover;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
}

.cursorHide {
  cursor: none;
  transition: cursor 2s;
}

.atari {
  font-family: "hyperspaceregular";
  letter-spacing: 1px;
}
@media screen and (max-width: 414px) {
  .atari {
    font-size: 0.75em;
  }
}

#scorecard {
  position: absolute;
  top: 0px;
  right: 20px;
  display: block;
  height: 70px;
  font-weight: 700;
  padding: 10px;
  text-align: right;
  text-shadow: 2px 2px #000000;
  font-size: 1.5em;
}

#spaceship {
  display: block;
  position: absolute;
  left: -100px;
  top: -100px;
  fill: rgba(255, 255, 255, 0.6);
  width: 20px;
  height: 13px;
}

#ufoShip {
  display: block;
  position: absolute;
  left: -100px;
  top: -100px;
  fill: rgba(0, 145, 0, 0.4);
}

#spawn {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 200px;
  height: 200px;
  transform: translate(-100px, -100px);
}

.rockAnim {
  display: block;
  font-size: 2em;
  position: absolute;
  left: -100px;
  top: -100px;
  fill: rgba(255, 255, 255, 0.6);
  font-family: "hyperspaceregular";
}

.rock100 {
  width: 100px;
  height: 100px;
}

.rock50 {
  width: 50px;
  height: 50px;
}

.rock25 {
  width: 25px;
  height: 25px;
}

.spaceShipShot,
.ufoShot {
  display: block;
  position: absolute;
  left: -100px;
  top: -100px;
}

.rockAnim span {
  position: relative;
  font-weight: 700;
  font-size: 2em;
  font-family: "hyperspaceregular";
  top: 12%;
  left: 12%;
}

.container {
  position: relative;
  z-index: 20;
}

h1 {
  font-weight: 300;
  margin-left: 30px;
  margin-top: 30px;
  position: relative;
  z-index: 10000;
  color: white;
}
h1 .game__title {
  opacity: 0.3;
}

#gameOverBoard.open {
  opacity: 1;
  pointer-events: all;
  transform: translate(-50%, -50%) scale(1);
}

#gameOverBoard {
  position: absolute;
  display: flex;
  opacity: 0;
  pointer-events: none;
  top: 50%;
  left: 50%;
  z-index: 100;
  transform: translate(-50%, -50%) scale(0.1);
  transition: all 0.5s ease-out;
  flex-direction: column;
  justify-content: center;
  border: 10px solid rgba(255, 255, 255, 0.3);
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 100%;
  height: 300px;
  width: 300px;
}
#gameOverBoard #gameover {
  text-shadow: 2px 2px #000000;
  font-weight: 900;
  margin: 0 auto;
}
#gameOverBoard #highScore {
  margin: 0 auto;
  margin-top: 5px;
  text-shadow: 2px 2px #000000;
  font-family: "hyperspaceregular";
  font-size: 1.25em;
}
#gameOverBoard #startOver {
  width: 150px;
  font-family: "hyperspaceregular";
  background: white;
  border-radius: 12px;
  margin: 0 auto;
  margin-top: 15px;
  opacity: 0.5;
}
#gameOverBoard #startOver:hover {
  opacity: 1;
  font-weight: 700;
}

p {
  margin-left: 30px;
}

#controls {
  position: absolute;
  z-index: 10000;
  margin: 0 auto;
  bottom: 50px;
  width: 350px;
}

.cntrlButton {
  margin-right: 20px;
}

cntrlButton:last-child {
  margin-right: 0px;
}

#gamepad {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 175px;
  width: 100%;
  display: none;
}
#gamepad .gameBtn {
  width: 50px;
  height: 50px;
  position: absolute;
  color: #d60808;
  border: 1px solid white;
  display: block;
  z-index: 1000;
  background: url("../img/arrow-alt-down.png") center no-repeat;
  background-size: cover;
  background-color: rgba(215, 215, 215, 0.75);
  border-radius: 50%;
}
#gamepad .gameBtn .glyphicon {
  position: relative;
  top: 30%;
  left: 15px;
  z-index: -1000;
  display: none;
}
#gamepad #btnUp {
  top: 5px;
  left: 75px;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
#gamepad #btnDown {
  top: 85px;
  left: 75px;
}
#gamepad #btnLeft {
  top: 45px;
  left: 15px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
#gamepad #btnRight {
  top: 45px;
  left: 137px;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
#gamepad #btnHS {
  top: 5px;
  right: 20px;
  background: url("../img/h2.png") center no-repeat;
  background-size: cover;
  background-color: rgba(215, 215, 215, 0.75);
}
#gamepad #btnShoot {
  top: 85px;
  right: 20px;
  background: url("../img/shoot.png") center no-repeat;
  background-size: cover;
  background-color: rgba(215, 215, 215, 0.75);
}

.modal {
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease-out;
  font-family: "hyperspaceregular";
  z-index: 10000;
}

.modal p {
  font-size: 1rem;
  font-family: "hyperspaceregular", Arial, Helvetica, sans-serif;
}

.modal a {
  color: rgba(215, 215, 215, 0.75);
  text-decoration: none;
}

.modal a:hover,
.modal a:focus {
  text-shadow: white 1px 0 20px;
}

.modal p:first-of-type {
  font-weight: 800;
}

.modal.open {
  pointer-events: all;
  opacity: 1;
}

.modal-content {
  padding: 20px;
}

.modal-dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 500px;
  font-size: 2rem;
  background-color: rgba(48, 44, 44, 0.7);
  transform: translate(-50%, -50%) scale(0.25);
  transition: all 1s ease-out;
  color: rgba(215, 255, 44, 0.9);
  background-color: rgba(48, 44, 44, 0.7);
  border: 10px solid rgba(215, 215, 215, 0.75);
  border-radius: 20%;
}

.modal-dialog h5 {
  font-weight: 900;
}

.modal-dialog.open {
  transform: translate(-50%, -50%) scale(1);
}

.modal-footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialogCloseBtn {
  margin-top: 20px;
  padding: 10px;
  background-color: green;
  color: white;
  border: 2px solid black;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 2em;
    margin: 5px 0px 0px 5px;
  }

  #gamepad {
    display: block;
  }

  #highScore {
    left: 12px !important;
  }
}
@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (orientation: portrait) {
  highScore {
    left: 12px !important;
  }
}
@media screen and (max-width: 414px) {
  #highScore {
    left: 12px !important;
  }

  #spawn {
    width: 100px;
    height: 100px;
    transform: translate(-50px, -50px);
  }
}